<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>显示商家评分详细信息</li>
      <li>可查看商家评分构成详细信息</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>店铺名称</label>
        <el-input v-model="queryParams.storeName" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
      <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <section class="cms-batch">
      <cms-button size="small" @click="batchUpdate" type="success" v-prem="'/shopStatistics/save'">保存修改</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" 
     @selection-change='getCheckItems'  ref='dataTable' >
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="store.id" label="商家ID" width="100"></el-table-column>
      <el-table-column prop="store.storeName" label="店铺名称" min-width='160'></el-table-column>
      <el-table-column prop="service" label="商家服务态度" min-width="110">
        <template slot-scope="scope">
          <div class="comment-value comment-value-inner">
            <el-input-number v-model="scope.row.service" :min="0" :max="5" :precision="1" :step="0.1" ></el-input-number>
          </div>
        </template>
      </el-table-column>
     <el-table-column prop="express" label="商家发货速度" min-width="110">
        <template slot-scope="scope">
           <div class="comment-value comment-value-inner">
            <el-input-number v-model="scope.row.express" :min="0" :max="5" :precision="1" :step="0.1"></el-input-number>
          </div>
        </template>
      </el-table-column>
     <el-table-column prop="matchV" label="商家符合度" min-width="110">
        <template slot-scope="scope">
          <div class="comment-value comment-value-inner">
            <el-input-number v-model="scope.row.matchV" :min="0" :max="5" :precision="1" :step="0.1"></el-input-number>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="exp" label="经验值" min-width="110">
        <template slot-scope="scope">
          <el-input v-model="scope.row.exp" style="width:80px;" maxlength="6"></el-input>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button icon='edit' size='small' type='info' @click.stop="$routerLink('/shopStatistics/view','update',{id:scope.row.store.id})">查看评分构成</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { shopStatisticsList, storeStatisticsUpdate } from '@/api/shop/shopStatistics.js'
var defaultData = {
  storeName: '',
  size: 10,
  page: 1
}
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    batchUpdate() {
      if (this.checkItems.length === 0) {
        this.$errorMessage('至少选择一条数据')
      } else {
        const updateArr = []
        const reg = /^[0-9]{0,6}$/
        let flag = true// 检测经验值项是否符合规则
        this.checkItems.forEach(v => {
          if (!reg.test(v.exp)) {
            this.$errorMessage('经验值只能为正整数，且不超过六位数')
            flag = false// 格式不正确是开关为false
          } else {
            const tempObj = {
              exp: v.exp,
              express: v.express,
              matchV: v.matchV,
              service: v.service,
              id: v.store.id
            }
            updateArr.push(tempObj)
          }
        })
        if (flag) {
          storeStatisticsUpdate({ storeStatisticeList: updateArr }).then(res => {
            if (res.code === 200) {
              this.$successMessage('修改成功')
              this.getTableData(shopStatisticsList, this.queryParams)
            } else {
              this.$errorMessage(res.message)
            }
          })
        }
      }
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(shopStatisticsList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(shopStatisticsList, this.queryParams)
  }
}
</script>

<style>
</style>



