// 店铺权益相关接口
import axios from '../fetch'

// 商家评分详细信息分页
export const shopStatisticsList = params => { return axios.get('/admin/platform/storeStatistics/page', { params: params }) }

// 修改商家评分详细信息
export const storeStatisticsUpdate = params => { return axios.post('/admin/platform/storeStatistics/update', params) }

// 查看商家评分构想详细信
export const commentList = params => { return axios.get('/admin/platform/comment/page', { params: params }) }

// 修改商家评分构想详细信
export const commentUpdate = params => { return axios.post('/admin/platform/comment/update', params) }




